<template>
  <full-height-layout>
    <div slot="layout_title">发票</div>

    <listing-view-top 
      slot="layout_inner_head" 
      :_ordering="ordering" 
      :_condition="condition" 
      @loadData_Father="loadData"
      @sortOption_Father="sortOption"  
    ></listing-view-top>
  
    <listing-view-body 
    
      :_listingList="listingList"  
      slot="layout_inner_body"
      @option_Father="option"
      
      v-if="!isLoading"
    >
    </listing-view-body>
  
    <transition v-else name="fade" slot="layout_inner_body">
      <loading></loading>
    </transition>

    <!-- 其它插件 -->
    <div slot="layout_extra">

      <trash @trashItem="_trash"></trash>
      
    </div>
  </full-height-layout>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import ListingViewTop from './Top/ListingViewTop.vue'
import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import ListingViewBody from './Body/ListingViewBody.vue'

import Trash from '@/components/Modal/Trash.vue'
import Loading from '@/components/Shimmer/Loading.vue'

export default {
  components: { 
    ListingViewTop,
    FullHeightLayout,
    ListingViewBody,
    Trash,
    Loading
  },
  name: 'ListingView',
  data() {
    return {
      listingList: [],
      
      ordering: '-add_time',
      condition: { 
        search: { }
      },
      activeId: 0,
      isLoading: true
    }
  },
  mounted() {
    this.loadData()
  },
  methods: {
    // 初始化
    buildFilter() {
      const res = this.condition.search
      return res
    },
    loadData() {
      const filter = this.buildFilter()
      
      setTimeout(() => {
        conn.listing.getList_Listing(filter, true, this.ordering).then(res => {
          this.listingList = res
          this.isLoading = false
        })
      }, config.delayTime)
    },
    // 过滤
    sortOption(v) {
      this.ordering = v
    },
    // 数据操作
    option(id, option) {
      this.activeId = id

      if (option == 'update') {
        // alert('x')
      } else if (option == 'trash') {
        tool.showModal('trashModal')
      }
    },
    _trash() {
      const _this = this
      tool.closeModal('trashModal')

      if ((this.activeId != null) && (this.activeId > 0)) {

        conn.listing.trash(this.activeId).then(res => {
          if (conn.judge_response(res)) {

            const id = res['id']
            tool.showToast('删除成功！！')
            _this.isLoading = true
            _this.loadData()
              
          }
        })

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


