<template>

  <table class="table m-0 table-top-line-none">
    <tbody>
      <tr v-for="(v, index) in _listingList" :key="v.id">
        <td width="6%" class="text-accent">{{ index + 1 }}</td>
        <td width="10%">{{ v.num }}</td>
        <td width="20%">
          {{ ser_timed(v.listing_time) }}
          <div 
            class="tip-wrapper tip-pos-fix animate-shake hand" 
            v-html="ser_timed_now_month(v.listing_time, v.id)"
            @click="showTip"
          >
          </div>
        </td>
        <td width="10%">${{ v.result_price }}</td>
        <td width="10%">
          {{ v.price_collect.num }}
        </td>
        <td width="20%">
          {{ v.membery.num }}&nbsp;{{ v.membery.named }}
        </td>
        <td width="12%" class="text-center">
          <div>查看</div>
        </td>
        
        <td width="12%" class="text-center">

          <i @click="option(v.id, 'trash')" class="float-right">
            <remove></remove>
          </i>

          <i @click="option(v.id, 'update')" class="float-right">
            <pencil></pencil>
          </i>

        </td>
      </tr>
    </tbody>
  </table>
  
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'

import Loading from '@/components/Shimmer/Loading.vue'

import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'

export default {
  components: { Loading, Pencil, Remove },
  name: 'ListingViewBody',
  props: {
    _listingList: {
      type: Array
    }
  },
  methods: {
    ser_timed(v) {return tool.timed.ser_timed(v, true, true) },
    ser_timed_now_month(v, id) { 
      const res = tool.timed.is_now_month(v)
      
      if (res) {
        return `<div class="tip tip-success fw-b">!</div>`
      } 
      return ''
    },
    option(id, option) { this.$emit('option_Father' , id, option) },
    showTip() {
      tool.showToast('该发票是属于今月份的。')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


